  }

  .close-menu {
    background-color: rgba(0, 0, 0, 0);
    border: none;
    position: absolute;
    top: 16px;
    right: 30px;
  }

  .menu-nav {
    width: 100%;
    height: 100%;
    z-index: 100;
    background-color: var(--background-color);
    padding-left: 32px;
    padding-right: 32px;
    transition: transform, all .25s cubic-bezier(.19, 1, .22, 1);
    position: fixed;
    top: 0;
    left: 0;
    overflow-y: scroll;
    transform: translateX(0);
  }

  .menu-nav.is-open {
    top: 0;
    left: 100%;
    transform: translateX(0);
  }

  .nav-list {
    text-align: center;
    margin-bottom: auto;
    display: block;
  }

  .nav-link {
    color: var(--first-color);
    font-size: 40px;
