.backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 100;
  transition: opacity var(--transition), visibility var(--transition);
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modalProduct {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-close-btn-icon {
